import Vue from "vue";
import Router from "vue-router";
import Root from "@/components/Root";
import TextView from "@/components/TextView";
import SearchView from "@/components/SearchView";
import ImageView from "@/components/ImageView";
import Page from "@/components/Page";
import Lecture from "@/components/Lecture";
import Mobile from "@/components/m/Mobile";
import mText from "@/components/m/mText";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/textview",
      name: "root",
      component: Root,
      children: [
        {
          path: "textview",
          components: { view1: TextView, view2: SearchView }
        },
        {
          name: "textPage",
          path: "textPage/:id",
          components: { view1: TextView, view2: Page }
        },
        {
          name: "textLecture",
          path: "textPage/:id/lecture",
          components: { view1: Lecture, view2: Page }
        },
        { path: "image", components: { view1: SearchView, view2: ImageView } },
        {
          name: "imagePage",
          path: "imagePage/:id",
          components: { view1: Page, view2: ImageView }
        },
        {
          name: "imageLecture",
          path: "imagePage/:id/lecture",
          components: { view1: Page, view2: Lecture }
        }
      ]
    },
    {
      path: "/m",
      name: "mobile",
      redirect: "m/mtext",
      component: Mobile,
      children: [
        {
          name: "mText",
          path: "mtext",
          components: { view1: mText }
        }
      ]
    }
  ]
});



