<template>
<VuePerfectScrollbar class="scroll-area"   >
    <table>
        <thead>
            <tr>
                <th v-on:click="reSort('title')" id="title-head"><a>{{titleHead}}</a></th>
                <th  v-on:click="reSort('school')"  id="school-head">{{schoolHead}}</th>
                <th v-on:click="reSort('year')" id="year-head">{{yearHead}}</th>
            </tr>
        </thead>
      
       
              <transition-group name='list-complete' tag="tbody" mode="out-in" >
            <tr v-for="listName in filteredItems" :key="listName.number "    mode="out-in"  class="list-complete-item">
                <td class="title ">     
                    <router-link class="underline" :to="{name:'textPage',params:{id:listName.number}}">{{listName.title}}</router-link>
                </td>
           
                 <td class="school">     
                    <router-link :to="{name:'textPage',params:{id:listName.number}}">{{listName.school}}</router-link>
                </td>
                   <td class="year">     
                    <router-link :to="{name:'textPage',params:{id:listName.number}}">{{listName.year}}</router-link>
                </td>
                
            </tr>
              </transition-group>
       
      
    </table>
</VuePerfectScrollbar>
</template>

<script>
import VuePerfectScrollbar from "vue-perfect-scrollbar";
import VueStroll from "vue-stroll";
export default {
  components: {
    VuePerfectScrollbar
  },
  created() {
    this.$http.get("/api/list/rand").then(response => {
      this.listNames = response.data;
      for (let i = 0; i < this.listNames.length; i++) {
        this.listNames[i].year = this.listNames[i].year.toString();
        this.listNames[i].number = this.listNames[i].number.toString();

        if (this.listNames[i].title == "") {
          this.listNames[i].title = this.listNames[i].title_2;
        }
      }
      console.log(this.listNames);
    });

    this.$EventBus.$on("searchThing", this.onReceive);
  },
  data() {
    return {
      searchThing: "",
      listNames: [],
      titleHead: "Title",
      schoolHead: "School",
      yearHead: "Year",
      tagName: ["title", "school"],
      isReverseTag: {
        title: false,
        school: false,
        year: false
      }
    };
  },
  methods: {
    onReceive: function(e) {
      console.log(e);
      this.searchThing = e;
    },
    reSort: function(tag) {
      //   if (this.isReverseTag[tag] == false) {
      //     this.$http.get("/api/list/" + tag).then(response => {
      //       this.listNames = response.data;
      //     });
      //     this.isReverseTag[tag] = true;
      //   } else {
      //     this.$http.get("/api/list/" + tag + "/reverse").then(response => {
      //       this.listNames = response.data;
      //     });
      //     this.isReverseTag[tag] = false;
      //   }

      this.listNames = _.orderBy(this.listNames, tag, "asc");
    },
    scrollHanle(evt) {
      //   console.log(evt)
    }
  },
  computed: {
    filteredItems() {
      if (typeof this.searchThing == "number") {
        this.searchThing = this.searchThing.toString();
      }
      let a = this.searchThing.toLowerCase();
      return this.listNames.filter(item => {
        return (
          (item.title.indexOf(a) &&
            item.title_2.indexOf(a) &&
            item.lecture.indexOf(a) &&
            item.name.indexOf(a) &&
            item.year.indexOf(a) &&
            item.school.indexOf(a) &&
            item.number.indexOf(a)) > -1
        );
      });
    }
  }
};
</script>

<style>
.list-complete-item {
  transition: opacity 0.5s;
}
.list-complete-enter,
.list-complete-leave-to {
  opacity: 0;
}
.list-complete-leave-active {
}
table {
  width: 100%;

  padding-top: 12px;
  table-layout: fixed;
}
th {
  text-align: left;
  font-weight: 500;
  cursor: pointer;
  width: 100%;
  font-size: 0.85em;
  height: 30px;
  vertical-align: top;
}
tbody,
thead {
  width: 100%;
}
thead {
  height: 30px;
}
tbody a {
  color: black;
  font-size: 0.85em;
  font-weight: 500;
}
tr {
  height: 25px;
}
td {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.title {
  padding-right: 50px;
  padding-left: 20px;
}
#title-head {
  padding-left: 20px;
  width: 72%;
}
#school-head {
  width: 18%;
}
#year-head {
  width: 10%;
}
.scroll-area {
  position: relative;
  width: 100%;
  height: 100%;
}
</style>



