<template>
<div id="search-outer">

   <div id="search-page" v-show ="searchState=='search'"  >
         <transition name="search-trans" >
        <input v-on:input="typing" @blur="changeSearchState('default')" ref="searchInput"  v-bind:value="searchInput" placeholder="Search">
        </transition>
      </div>
      
    <div v-show ="searchState=='default'" id="search-container">

<div id="button-container">
        <div v-on:click="changeSearchState('about')" id="about-button" >
            About
        </div>

        <div v-on:click="changeSearchState('search')" id= "search-button">
        Search
        </div>
         
   
      </div>
    
 </div>
     <transition name="slide-up" mode="in-out">
<About v-show ="searchState=='about'">

</About>
 </transition>
</div>
</template>

<script>
import About from "./About";

export default {
  components: {
    About
  },
  data() {
    return {
      searchInput: "",
      searchState: "default"
    };
  },
  methods: {
    changeSearchState: function(e) {
      this.searchState = e;
    },
    typing(e) {
      this.message = e.target.value;
      this.$EventBus.$emit("searchThing", this.message);
    }
  },
  created() {
    this.$EventBus.$on("state-default", () => {
      this.searchState = "default";
    });
  },
  watch: {
    searchState: function(val) {
      if (val == "search") {
        this.$nextTick(() => this.$refs.searchInput.focus());
      }
    }
  }
};
</script>

<style>
#search-outer {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
  flex-direction: column-reverse;
  position: relative;
}

#search-container {
  display: flex;
  align-items: flex-end;

  flex-direction: column-reverse;

  padding-right: 20px;
  padding-bottom: 20px;
}
#button-container {
  align-items: flex-end;
  cursor: pointer;
}
#about-button {
  display: inline;
  margin-right: 20px;
}
#search-button {
  display: inline;
}
#search-page {
  top: 0;
  left: 0;
  position: absolute;
}
#search-page input {
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  width: 300px;
  height: 50px;

  font-size: 1.2em;
  padding-left: 10px;
}
#search-page input:focus {
  border: 0;
  outline: none !important;
}
.slide-up-enter-active {
  transition: margin-bottom 0.4s ease-in;
}

.slide-up-leave-active {
  transition: opacity 0.4s ease-out;
}
.slide-up-leave {
  opacity: 1;
}
.slide-up-leave-to {
  opacity: 0;
}
.slide-up-enter {
  margin-bottom: 100%;
}
.slide-up-enter-to {
  margin-bottom: 0px;
}

.search-trans-enter-active {
  transition: width 0.4s ease-in;
}
.search-trans-enter {
  width: 0;
}
.search-trans-enter-to {
  width: 300px;
}
</style>



