<template>
<div id="container">
   <div id="nav">
       <div id="left-nav">
           <div id= "h1-container">
          <router-link to="/"><p>LIST OF GRAPHIC DESIGN STUDENT WORKS</p></router-link>
           </div>
           <div id="show-select-1">
               <p>Show by:</p>
           </div>
           <div id="show-select-2">
              <router-link to="/textview"> <p>Text List</p></router-link>
           </div>
           <div id="show-select-3">
                <router-link to="/image"> <p>Image</p></router-link>
           </div>
           
       </div>
       <div id="right-nav">
            <p  class="hover-button">Sort by:</p>
            <p  v-on:click="dropDown('showall')" class="hover-button">Show All</p>
            <p v-on:click="dropDown('school')" class="hover-button">School</p>
            <p v-on:click="dropDown('media')" class="hover-button">Media</p>
            <p v-on:click="dropDown('lecture')" class="hover-button">Lecturer</p>
            <p v-on:click="dropDown('year')" class="hover-button">Year</p>
       </div>
   </div>
   
   <div id="view">
       <div id="left-view" class="views" >
           <keep-alive>
  <router-view class="view-1" name="view1" ></router-view>
  </keep-alive>
       </div>
      
       <div id="right-view" class="views">
           <transition-expand>
           <div v-show="dropActive.school" name="expand" class="drop-down-container"  ref="drop">
             <p id="drop-head">LIST OF SCHOOL</p>
             <div class="drop-down-inner">
               <div class="drop-downbox" v-on:click="SortThing(dropIndexs)" v-for="dropIndexs in dropIndexSchool">
               <p >{{dropIndexs}}</p>
               </div>
           </div>
           </div>
           </transition-expand>

           <transition-expand>
           <div  v-show="dropActive.media" name="expand" class="drop-down-container"  ref="drop">
               <p id="drop-head">LIST OF FIELD</p>
             <div class="drop-down-inner">
              <div class="drop-downbox" v-on:click="SortThing(dropIndexs)" v-for="dropIndexs in dropIndexMedia">
               <p >{{dropIndexs}}</p>
               </div>
             </div>
           </div>
           </transition-expand>

           <transition-expand>
           <div  v-show="dropActive.lecture" name="expand" class="drop-down-container"  ref="drop">
               <p id="drop-head">LIST OF Lecturer</p>
             <div class="drop-down-inner">
               <div class="drop-downbox" v-on:click="SortThing(dropIndexs)" v-for="dropIndexs in dropIndexLecture">
               <p >{{dropIndexs}}</p>
               </div>
               </div>
           </div>
           </transition-expand>
              <transition-expand>
           <div  v-show="dropActive.year" name="expand" class="drop-down-container"  ref="drop">
               <p id="drop-head">LIST OF Year</p>
             <div class="drop-down-inner">
               
                <div class="drop-downbox" v-on:click="SortThing(dropIndexs)" v-for="dropIndexs in dropIndexYear">
               <p >{{dropIndexs}}</p>
               </div>
               </div>
           </div>
           </transition-expand>
           
            <keep-alive>
                 <transition name="right" >
           <router-view  class="view-2" name="view2"></router-view>
           </transition>
             </keep-alive>
                

       </div>
   </div>
</div>
</template>

<script>
import TransitionExpand from "./TransitionExpand.vue";

export default {
  name: "root",
  components: {
    TransitionExpand
  },
  data() {
    return {
      expanded: false,
      dropActive: {
        school: false,
        class: false,
        lecture: false,
        year: false
      },
      dropIndexSchool: [],
      dropIndexLecture: [],
      dropIndexYear: [],
      dropIndexMedia: []
    };
  },
  methods: {
    SortThing: function(e) {
      // this.message = e.target.value;
      this.dropActive.year = false;
      this.dropActive.school = false;
      this.dropActive.lecture = false;
      this.dropActive.media = false;
      this.$EventBus.$emit("searchThing", e);
    },
    dropdownListMake: function() {
      this.$http.get(`/api/list/schoolindex`).then(response => {
        var receiveData = response.data;
        for (var i = 0; i < receiveData.length; i++) {
          this.dropIndexSchool.push(receiveData[i].school);
        }
        console.log(this.dropIndexSchool);
      });
      this.$http.get(`/api/list/lectureindex`).then(response => {
        var receiveData = response.data;
        for (var i = 0; i < receiveData.length; i++) {
          this.dropIndexLecture.push(receiveData[i].lecture);
        }
        console.log(this.dropIndexLecture);
      });
      this.$http.get(`/api/list/yearindex`).then(response => {
        var receiveData = response.data;
        for (var i = 0; i < receiveData.length; i++) {
          this.dropIndexYear.push(receiveData[i].year);
        }
        console.log(this.dropIndexYear);
      });
      this.$http.get(`/api/list/mediaindex`).then(response => {
        var receiveData = response.data;
        for (var i = 0; i < receiveData.length; i++) {
          this.dropIndexMedia.push(receiveData[i].media);
        }
        console.log(this.dropIndexMedia);
      });
    },
    dropDown: function(e) {
      console.log(this.$refs.drop);
      if (e == "school") {
        if (this.dropActive.school == false) {
          this.dropActive.school = true;
          this.dropActive.lecture = false;
          this.dropActive.media = false;
          this.dropActive.year = false;
        } else {
          this.dropActive.school = false;
        }
      } else if (e == "lecture") {
        if (this.dropActive.lecture == false) {
          this.dropActive.lecture = true;
          this.dropActive.school = false;
          this.dropActive.media = false;
          this.dropActive.year = false;
        } else {
          this.dropActive.lecture = false;
        }
      } else if (e == "media") {
        if (this.dropActive.media == false) {
          this.dropActive.media = true;
          this.dropActive.school = false;
          this.dropActive.lecture = false;
          this.dropActive.year = false;
        } else {
          this.dropActive.media = false;
        }
      } else if (e == "year") {
        if (this.dropActive.year == false) {
          this.dropActive.year = true;
          this.dropActive.school = false;
          this.dropActive.lecture = false;
          this.dropActive.media = false;
        } else {
          this.dropActive.year = false;
        }
      } else if (e == "showall") {
        this.SortThing("");
      }
    }
  },
  created() {
    this.dropdownListMake();
  },
  mounted() {
    // this.$nextTick(function() {
    //   let a = this.$el.querySelectorAll(".drop-down-container");
    //   let path = this.$router.history.current.path;
    //   let res = path.split("/");
    //   console.log(res);
    //   if (res[1] == "image" || res[1] == "imagePage") {
    //     for (let i = 0; i < a.length; i++) {
    //       a[i].style.position = "relative";
    //     }
    //   }
    // });
  }
};
</script>

<style>
::-webkit-scrollbar {
  display: none;
}
#container {
  width: 100%;
  height: 100%;
}
#nav {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  width: 100%;
  height: 5%;
  border-bottom: black solid 1px;
}
#left-nav {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: 0.9em;
}
#left-nav a {
  color: black;
}
#h1-container {
  width: 54%;
}
#h1-container p {
  padding-left: 20px;
  margin: 0;
  font-weight: 500;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

#show-select-1 {
  width: 18%;
}
#show-select-2 {
  width: 18%;
}
#show-select p {
  display: inline;
}
.drop-down-container {
  width: 100%;
  position: fixed;
  background-color: white;
  z-index: 2;
  border-bottom: 1px solid black;
}
#drop-head {
  padding-left: 20px;
  margin-bottom: 10px;
  margin-top: 14px;
  height: 28px;
}
.drop-down-inner {
  padding-left: 20px;
  width: 38%;
  column-count: 4;
  -moz-column-count: 4;
  -webkit-column-count: 4;
  overflow: auto;
  margin: 0;
  padding-bottom: 10px;
}
.drop-downbox {
  width: 100%;
  margin-bottom: 10px;
}
.drop-down-container p {
  font-size: 0.9em;
  margin: 0;
  cursor: pointer;
}
.hover-button {
  cursor: pointer;
}
#right-nav {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  float: left;
  width: 50%;
  height: 100%;
  border-left: black solid 1px;

  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
}
#right-nav p {
  display: inline;
  width: 18%;
  font-size: 0.9em;
}
#right-nav p:nth-child(1) {
  width: 18%;
}
#view {
  width: 100%;
  height: 95%;
}
.views {
  width: 50%;
  height: 100%;
  float: left;
}
#left-view {
  overflow: auto;
}
#right-view {
  overflow: auto;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border-left: 1px black solid;
}
.expand-enter-active,
.expand-leave-active {
  transition-property: height;
}
.expand-enter,
.expand-leave-to {
}

.right-enter-active,
.right-leave-active {
  transition: opacity 0.5s;
}
.right-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.underline {
  display: inline;
  position: relative;
  overflow: hidden;
}
.underline:after {
  content: "";
  position: absolute;
  z-index: -1;
  right: 0;
  width: 0;
  bottom: -2px;
  background: #000;
  height: 1px;
  transition-property: width;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
}
.underline:hover:after,
.underline:focus:after,
.underline:active:after {
  left: 0;
  right: auto;
  width: 100%;
}
</style>



