<template>
    <div id="page-container">
      <!-- <div id="page-load"></div> -->
        <div id="page-title-container">
            <p>{{pageData.title}}</p>
                        <p>{{pageData.title_2}}</p>

        </div>

       <!-- <iframe frameborder=0 framespacing=0 marginheight=0 marginwidth=0 scrolling=no vspace=0 src="https://drive.google.com/file/d/1Ib7-SibKqYs2lR_50rc2hai80_UJuVvG/preview" width="640" height="360"></iframe> -->
         <swiper :options="swiperOption" ref="mySwiper" v-show="useVideo==''" >
        <swiper-slide  id="page-image-container" v-for="photos in evenNumbers" :key="photos.id">  
              <img v-on:click="bigPhoto(photos*2-1)" :id="`image-${photos*2-1}`"  class = "image-view-inner image-left"  :src="`http://35.211.120.96/images/batch_${pageData.number}_${photos*2-1}.png`">
              <img v-on:click="bigPhoto(photos*2)"   :id="`image-${photos*2}`" class = "image-view-inner  image-right"  :src="`http://35.211.120.96/images/batch_${pageData.number}_${photos*2}.png`" v-if="photos*2<photoNum">
        </swiper-slide>
        </swiper>
                <div v-show="useVideo==''"  id='swiper-button'><a v-on:click="prevPage"><img id="left-slide-button" src="@/assets/arrow_left_mini.svg"></a>
                <div id="swipe-num-display"><span>{{nowPhotoNum}}/{{evenNumbers}}</span></div>  
                  <a v-on:click="nextPage"><img id="right-slide-button" src="@/assets/arrow_right_mini.svg"></a>
                  </div>
        <!-- <div id= "arrow">
            <div id ="arrow-inner">
            <div id="arrow-left">
                <img src="@/assets/arrow_left.svg" alt="">

                </div>
                 <div id="arrow-right">
                     <img src="@/assets/arrow_right.svg" alt="">
                </div>
                </div>
        </div> -->

        <div id="page-statement-container">
            <p>{{pageData.statement}}</p>
        </div>
         <div id="page-subscript-head">
                <p>Instructions</p>
            </div>
        <div id="page-subscript-container">
            <p>{{pageData.subscript}}</p>
        </div>

        <div id="page-etc-container">
            <div id="page-lecture" >
                <p>Lecturer</p>
                  
                <span v-on:click="linkToLecture" v-show="pageData.lecture!='미기재'" class="underline">{{pageData.lecture}} {{pageData.lecture_2}}</span>
                 
                <p v-show="pageData.lecture=='미기재'">미기재</p>
            </div>

            <div id="page-student">
                <p>Student</p>
                <span v-on:click="linkToList(pageData.name)" class="underline">{{pageData.name}} {{pageData.name_2}}</span>
            </div>

            <div id="page-school">
                <p>School</p>
                <span v-on:click="linkToList(pageData.school)" class="underline">{{pageData.school}} {{pageData.school_2}}</span>
            </div>

            <div id="page-class">
                <p>Class</p>
                <span>{{pageData.class}}</span>
            </div>

            <div id="page-year">
                <p>Year</p>
                <span v-on:click="linkToList(pageData.year)" class="underline">{{pageData.year}}</span>
            </div>
        </div>
        <transition name="fade">
<div   v-show="bigPhotoActivate" id="big-photo-container">
  <div id="big-close-button-container" v-on:click="closeBigPhoto"><img class="close-button-animation" src="@/assets/close.svg"></div>
  <img :src="bigPhotoUrl"  v-on:click="nextBigPhoto" >
</div >
</transition>
    </div>
</template>

<script>
import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  components: {
    swiper,
    swiperSlide
  },
  created() {
    this.photoNumGet();
    this.pageDataGet();
  },
  data() {
    return {
      nowBigPhotoNum: 0,
      nowPhotoNum: 0,
      allPageNum: 0,
      pageData: {},
      photoNum: 0,
      photoSrc: [],
      videoSrc: "",

      useVideo: false,
      swiperOption: {
        spaceBetween: 50,
        simulateTouch: false
      },
      bigPhotoActivate: false,
      bigPhotoUrl: ""
    };
  },
  watch: {
    $route(to, from) {
      this.photoNumGet();
      this.pageDataGet();
      this.swiper.slideTo(0, 0);
      this.bigPhotoActivate = false;
    }
  },
  methods: {
    SortThing: function(e) {
      // this.message = e.target.value;
      console.log(e);
      this.$EventBus.$emit("searchThing", e);
    },
    linkToLecture: function() {
      let path = this.$router.history.current.path;
      let res = path.split("/");
      let imgOrText = res[1];
      if (imgOrText == "imagePage") {
        this.$root.$router.push({ name: "imageLecture" });
      } else {
        this.$root.$router.push({ name: "textLecture" });
      }
    },
    linkToList: function(e) {
      let path = this.$router.history.current.path;
      let res = path.split("/");
      let imgOrText = res[1];
      if (imgOrText == "imagePage") {
        this.$root.$router.push({ name: "imagePage" });
        this.SortThing(e);
      } else {
        this.$root.$router.push({ name: "textPage" });
        this.SortThing(e);
      }
    },
    closeBigPhoto: function() {
      this.bigPhotoActivate = false;
    },
    nextBigPhoto: function() {
      this.nowBigPhotoNum++;
      if (this.nowBigPhotoNum < this.photoNum) {
        this.bigPhotoUrl = `http://35.211.120.96/images/batch_${
          this.pageData.number
        }_${this.nowBigPhotoNum}.png`;
      } else {
        this.closeBigPhoto();
      }
    },
    bigPhoto: function(f) {
      this.nowBigPhotoNum = f;
      this.bigPhotoUrl = `http://35.211.120.96/images/batch_${
        this.pageData.number
      }_${this.nowBigPhotoNum}.png`;
      this.bigPhotoActivate = true;
    },
    pageDataGet: function() {
      var id = this.$route.params.id;
      this.$http.get(`/api/list/page/${id}`).then(response => {
        this.pageData = response.data[0];
      });
    },
    photoNumGet: function() {
      var id = this.$route.params.id;
      this.$http.get(`/api/list/page/${id}/photo`).then(response => {
        this.photoNum = response.data[0].photo;
        this.useVideo = response.data[0].video;
        console.log(this.useVideo);
        this.videoSrc = response.data[0].url;
        if (this.videoSrc != "") {
          this.photoNum++;
          console.log(this.videoSrc);
        }
      });
    },
    imageRowMake: function() {
      let container = document.querySelector("page-image-container");
    },
    nextPage: function() {
      this.swiper.slideNext();
      this.nowPhotoNum = this.swiper.activeIndex + 1;
    },
    prevPage: function() {
      this.swiper.slidePrev();
      this.nowPhotoNum = this.swiper.activeIndex + 1;
    }
  },
  computed: {
    evenNumbers: function() {
      var returnNum = 0;
      if (this.photoNum % 2 == 0) {
        returnNum = this.photoNum / 2;
      } else {
        returnNum = this.photoNum / 2 + 0.5;
      }

      console.log(this.photoNum);
      console.log(returnNum);
      return returnNum;
    },
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {},
  updated() {
    this.$nextTick(function() {
      this.nowPhotoNum = this.swiper.activeIndex + 1;
    });
  }
};
</script>

<style >
#page-load {
  background-color: white;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 100;
}
#page-container p {
  font-size: 0.9em;
  line-height: 1.7;

  margin: 0;
  word-break: keep-all;
}
#page-container {
  margin-left: 20px;
  padding-top: 15px;
  padding-right: 20px;
  padding-bottom: 50px;
  position: relative;
}
#arrow {
  position: relative;
  height: 80px;
  width: 100%;
  margin-top: 20px;
}
#arrow-inner {
  margin: 0 auto;
  width: 70%;
}
#arrow img {
  width: 40px;
}
#arrow-left {
  width: 40%;
  float: left;
  height: 60px;
}
#arrow-right {
  width: 40%;
  float: right;
  height: 60px;
}
#arrow-right img {
  float: right;
}
#left-slide-button {
  width: 15px;
  margin-right: 20px;
}
#right-slide-button {
  width: 15px;
  margin-left: 20px;
}
.swiper-container {
  height: auto;
}
.swiper-wrapper {
  height: auto;
}
.swiper-slide {
  height: auto;
}
#page-image-container {
  width: 100%;
  display: flex;
}
#page-image-container img {
  width: 49%;
  align-self: center;
  object-fit: contain;
  cursor: crosshair;
}

#page-image-container img:nth-child(2n-1) {
  margin-right: 2%;
}

#swiper-button {
  cursor: pointer;
  text-align: center;
  margin-top: 20px;
}
#swipe-num-display {
  width: 40px;
  display: inline-block;
}
#page-title-container {
  margin-bottom: 15px;
}
#page-title-container p {
  margin: 0;
  font-size: 1.35em;
  line-height: 1.3;
}
#page-statement-container {
  width: 60%;
  margin-top: 28px;
  margin-bottom: 30px;
}
#page-subscript-head {
}
#page-subscript-container {
  width: 60%;
  display: inline-block;
  vertical-align: top;
  margin-right: 5%;
}
#page-etc-container {
  width: 30%;
  display: inline-block;
}
.page-sort-button {
}
#page-lecture,
#page-student,
#page-school,
#page-class {
  margin-bottom: 8px;
}

#page-lecture,
#page-student,
#page-school,
#page-class :nth-child(2) {
  margin-bottom: 8px;
}

#page-etc-container span {
  font-size: 0.9em;
}

#big-photo-container {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-filter: none;
  -moz-filter: none;
  -o-filter: none;
  -ms-filter: none;
  filter: none;
}
#big-photo-container img {
  max-height: 80%;
  max-width: 80%;
  cursor: crosshair;
}
#big-photo-container img:hover {
}
#big-close-button-container {
  width: 40px;
  position: fixed;
  top: 40px;
  right: 40px;
  cursor: pointer;
  z-index: 7;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



