<template>
<div id="lecture-container">
  <div id="lecture-name">
    <p>{{lectureData.name}} {{lectureData.name_2}}</p>
  </div>
  <div id="lecture-profile">
    <p>{{lectureData.profile}} </p>
  </div>

  <div id="lecture-related">
    <p>Related Student Works</p>
    <p v-for="relative in relativeData" :key="relative.id">{{relative.title}}, {{relative.school}}, {{relative.year}}</p>
  </div>
</div>
</template>

<script>
export default {
  created() {
    var id = this.$route.params.id;
    this.$http.get(`/api/list/page/${id}/lecture`).then(response => {
      this.lectureData = response.data[0];
      console.log(response.data);
    });

    this.$http.get(`/api/list/page/${id}/relative`).then(response => {
      this.relativeData = response.data;
      console.log(response.data);
    });
  },

  data() {
    return {
      lectureData: {},
      relativeData: {}
    };
  }
};
</script>

<style>
#lecture-container p {
  font-size: 0.9em;
  line-height: 1.7;
  margin: 0;
  word-break: keep-all;
}

#lecture-container {
  padding-left: 20px;
  padding-top: 10px;
  padding-right: 20px;
  padding-bottom: 50px;
}
#lecture-name {
  font-size: 1.35em;
}
#lecture-profile {
  width: 60%;
  margin-top: 30px;
}
#lecture-related {
  width: 60%;
  margin-top: 150px;
}
</style>



