<template>
    <div  id="image-view-container" >
        <div v-show="loading" id="temp_wrapper"  >
        <div  v-for="listTitle in filteredItems" class="image-view-inner-container" :key="listTitle.id" >
            <router-link  :to="{name:'imagePage',params:{id:listTitle.number}}">
                 <p>{{listTitle.number}}.</p>
          <img   class = "image-view-inner" :src="'http://35.211.120.96/images/batch_'+listTitle.number+'_thumb.png'" >
       
            </router-link>
        </div>
        </div>
    </div>
</template>

<script>
export default {
  created() {
    this.$http.get("/api/list/rand").then(response => {
      this.listTitles = response.data;
      for (let i = 0; i < this.listTitles.length; i++) {
        this.listTitles[i].number = this.listTitles[i].number.toString();
        this.listTitles[i].year = this.listTitles[i].year.toString();
      }
    });
    // this.$sr.reveal('.image-view-inner-container');
    this.$EventBus.$on("searchThing", this.onReceive);
  },
  mounted() {
    this.$nextTick(function() {
      this.loading = true;
    });
  },
  data() {
    return {
      listTitles: [],
      searchThing: "",
      loading: false
    };
  },
  methods: {
    onReceive: function(e) {
      console.log(e);
      this.searchThing = e;
    }
  },

  computed: {
    filteredItems() {
      if (typeof this.searchThing == "number") {
        this.searchThing = this.searchThing.toString();
      }
      let a = this.searchThing.toLowerCase();
      return this.listTitles.filter(item => {
        return (
          (item.title.indexOf(a) &&
            item.title_2.indexOf(a) &&
            item.lecture.indexOf(a) &&
            item.name.indexOf(a) &&
            item.year.indexOf(a) &&
            item.school.indexOf(a) &&
            item.number.indexOf(a)) > -1
        );
      });
    }
  }
};
</script>

<style>
#image-view-container {
  height: 100%;
  min-height: 1000px;
}
#temp_wrapper {
  padding-left: 20px;
  padding-top: 20px;
  overflow: hidden;
  min-height: 100%;
}
#image-view-container img {
  height: 150px;

  /* box-shadow: 1px 1px 2px gray; */
}
#image-view-container p {
  margin: 0;
  font-size: 0.9em;
  word-wrap: break-word;
}
.image-view-inner-container {
  display: inline-block;
  margin-right: 15px;
  height: 205px;
}
</style>



