<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: "App",
  created() {
    if (
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      )
    ) {
      this.$router.push("/m");
    }
  }
};
</script>

<style>
@font-face {
  font-family: gdsw;
  src: url(./assets/default.woff) format("woff"),
    url(./assets/default.ttf) format("ttf"),
    url(./assets/default.otf) format("otf");
}
#app {
  font-family: gdsw, Helvetica, Arial, sans-serif;
  /* font-family: "Noto Sans KR", sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
  width: 100%;
  height: 100%;
}
body,
html {
  width: 100%;
  margin: 0;
  height: 100%;
}
#app a {
  text-decoration: none;
  color: black;
}
</style>



