<template>
    <div id="about-page">
         <div id="about-page-container">
      <div id="about-title-container">
            <p>LIST OF GRAPHIC DESIGN STUDENT WORKS</p>
                        <p>시각디자인학과 과제 리스트</p>

        </div>
  <div id="about-statement-container">
      <p> LIST OF GRAPHIC DESIGN STUDENT WORKS(시각디자인학과 과제 리스트)는 서울, 경기권
소재 대학교의 시각디자인학과에서 실제 이뤄지고 있는, 혹은 이뤄졌던 수업 과제물의
아카이브입니다. 이 리스트는 시각디자인학과 재학생과 졸업생들이 이수한 수업의 결과물을
공유해준 것으로, 게시된 작품에 관한 저작권은 학생에게, 수업 내용 및 커리큘럼에 관한
저작권은 수업 담당자에게 있습니다.</p> 
        </div>

         <div id="about-close-button"  v-on:click="$EventBus.$emit('state-default')"><img class="close-button-animation" src="@/assets/close.svg"></div>
         </div>
 </div>

</template>

<script>
export default {
  name: "About"
};
</script>

<style>
#about-close-button {
  width: 20px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  z-index: 7;
}

#about-page {
  padding-left: 20px;
  padding-top: 10px;
  height: 100%;
  width: 100%;
  text-align: left;
  position: relative;
}
#about-page-container {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
}
#about-page-container p {
  margin: 0;
}
#about-title-container {
  margin: 0;
  font-size: 1.35em;
}
#about-statement-container p {
  font-size: 0.9em;
  line-height: 1.7;
  width: 60%;
  margin-top: 20px;
  margin-bottom: 20px;
  word-break: keep-all;
}
.close-button-animation {
  -webkit-transition: -webkit-transform 0.4s ease-in-out;
  -ms-transition: -ms-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
}
.close-button-animation:hover {
  transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
}
</style>



